import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";
import { StyledImage } from "./styled";

function Author() {
  return (
    <Container>
      <Header title="O autorze" />
      <Section
        title={
          <a
            href="https://todo-zrozumiec-react.netlify.app"
            target="_blank"
            rel="noopener noreferrer"
          >
            Tomasz Piotrowski - moja inna lista zada w React'cie
          </a>
        }
        extraHeaderContent={
          <StyledImage
            src="https://image.freepik.com/free-vector/cute-cartoon-beaver-listening-music-with-headphone_290315-1409.jpg"
            alt="bober"
          />
        }
        children={
          <p>
            Cze, jestem technikiem informatyki. Pracowaem troszk w serwisie
            IT duzej firmy , ale nie wytrzymaem presji psychicznej. Pzniej 9
            lat jako specjalista ds. archiwizacji dokumentacji medycznej w znowu
            firmie oglnopolskiej, ostatnio mnie zwolniono i ucz si
            programowania frontendowego od 3 lat.
          </p>
        }
      />
    </Container>
  );
}

export default Author;
